
.header {
  background: #0B0B0B;
}


.contacts {
  background: #F6F6F8;
  padding-top: 110px;
  @media (max-width: 767.98px) {
    padding-top: 74px;
  }
  &__right {
    width: 750px;
    border-radius: 10px;
    background: #E4E4EF;
    padding: 43px 39px;
    @media (max-width: 1199.98px) {
      width: auto;
      flex: 1 1 auto;
    }
    @media (max-width: 1023.98px) {
      padding: 25px 20px;
    }
    @media (max-width: 767.98px) {
      width: 100%;
      padding: 32px 16px;
    }
    .title{
      color: #0B0B0B;
      font-size: 32px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      margin-bottom: 28px;
      @media (max-width: 767.98px) {
        font-size: 22px;
      }
    }
  }

  &__wrapper {
    display: flex;
    padding-bottom: 120px;
    @media (max-width: 1023.98px) {
      padding-bottom: 60px;
    }
    @media (max-width: 767.98px) {
      display: block;
    }
  }

  &__left {
    width: auto;
    flex: 1 1 auto;
    min-width: 295px;
    margin-right: 95px;
    @media (max-width: 1199.98px) {
      margin-right: 40px;
    }
    @media (max-width: 767.98px) {
      width: 100%;
     padding-bottom: 56px;
    }
  }

  &__phones {
    padding-bottom: 24px;

    a {
      display: block;
      color: #0B0B0B;
      padding-left: 34px;
      font-size: 22px;
      font-style: normal;
      font-weight: 400;
      line-height: 38px;
      transition: 0.5s;
      &:hover{
        color: #7339F5;
      }
    }
  }

  &__phones-title, &__email-title, &__office-title {
    display: flex;
    margin-bottom: 13px;

    svg {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }

    color: #0B0B0B;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  &__email {
    padding: 30px 0 17px;
    a {
      transition: 0.5s;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      &:hover {
        color: #7339F5;
      }
    }
  }

  &__office {
    padding-top: 30px;
    padding-bottom: 25px;
  }

  &__email, &__phones, &__office {
    border-bottom: 1px dotted #7B7B7B;
  }

  &__location {
    color: #0B0B0B;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    padding-left: 34px;
  }
  .header__socials{
    padding-top: 31px;
    padding-left: 40px;
    @media (max-width: 1023.98px) {
      padding-bottom: 0;
      margin: 0;
    }
  }
}

.title-main {
  margin-bottom: 30px;
  margin-top: 70px;
  @media (max-width: 1023.98px) {
    margin-top: 50px;
  }
  @media (max-width: 767.98px) {
    margin-top: 35px;
  }

  .subtitle {
    p {
      margin-bottom: 3px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }

  h1 {
    color: #0B0B0B;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 767.98px) {
      font-size: 30px;
      font-weight: 600;
      line-height: normal;
    }
  }
}

.form__row-content {
  display: flex;
  @media (max-width: 1023.98px) {
    flex-wrap: wrap;
  }
  .form__row-left{
    width: 323px;
    margin-right: 26px;
    @media (max-width: 1199.98px) {
      width: 50%;
    }
    @media (max-width: 1023.98px) {
      width: 100%;
      margin-right: 0;
    }
  }
  .form__row-right{
    width: 323px;
    @media (max-width: 1199.98px) {
      width: 50%;
    }
    @media (max-width: 1023.98px) {
      width: 100%;
    }
    @media (max-width: 767.98px) {
      margin-top: 20px;
    }
  }
  input{
    border-radius: 4px;
    border: 1px solid #E7E6E6;
    background: #FFF;
    width: 100%;
    height: 44px;
    color: #0B0B0B;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    transition: 0.5s;
    padding: 4px 5px 8px 24px;
  }
  textarea{
    min-height: 135px;
    border-radius: 4px;
    border: 1px solid #E7E6E6;
    background: #FFF;
    @media (max-width: 1023.98px) {
      min-height: 70px;
    }
  }
  .form-control label {
    display: flex;
    margin-bottom: 7px;
    align-items: center;
  }
  .form-control label svg {
    width: 18px;
    height: 18px;
    margin-right: 8px;
  }
  label span{
    color: #0B0B0B;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-left: 27px;
    span{
      color: #7339F5;
      padding-left: 0;
    }
  }
  .button-send{
    width: 100%;
    margin-top: 40px;
    margin-bottom: 50px;
    @media (max-width: 1023.98px) {
      margin-bottom: 0;
    }
  }
}
.form__row-content  input:active, .form__row-content  input:hover, .form__row-content  input:focus {
  border: 1px solid #7339F5;
}
.map{
  height: 496px;
  width: 100%;
}